import { Component, OnInit, OnDestroy } from '@angular/core';
import { FirebaseService } from '../firebase.service';
import { Artists } from '../../interface/about-me/Artists';
import { HttpErrorResponse } from '@angular/common/http';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { ObserableService } from '../obserable.service';

@Component({
  selector: 'app-artists',
  templateUrl: './artists.component.html',
  styleUrls: ['./artists.component.css'],
  animations: [
    trigger('spinner', [
      state('inactive', style({
        transform: 'scale(1)'
      })),
      state('active', style({
        opacity: 0,
        transform: 'scale(1)'
      })),
      transition('inactive => active', animate('500ms ease-in'))
    ]),
    trigger('card', [
      state('inactive', style({
        opacity: 0,
        transform: 'scale(1)'
      })),
      state('active', style({
        opacity: 1,
        transform: 'scale(1)'
      })),
      transition('inactive => active', animate('500ms ease-in'))
    ])
  ]
})
export class ArtistsComponent implements OnInit, OnDestroy {
  private artists: Artists = {};
  private loadingArtists: Boolean = true;
  private spinnerState = 'inactive';
  private cardState = 'inactive';


  constructor(private fbService: FirebaseService, private obsService: ObserableService) { }

  ngOnInit() {
    this.fbService.getArtists().subscribe((data: Artists) => {
      this.loadingArtists = false;
      this.artists = data;
      this.loadingOk();
    });
  }
  loadingOk() {
    this.spinnerState = 'active';
    this.cardState = 'active';
  }
  ngOnDestroy() {
    this.obsService.leftMenuOppen.next(false);
  }
}
